<template>
  <div class="nav-header">
    <van-nav-bar title="抵质押资料" left-text="返回" left-arrow @click-left="back"></van-nav-bar>
    <div class="con-main">
      <van-cell-group class="vcg-Inp">
        <van-field
          v-model="createDate"
          :disabled="editOrView"
          type="text"
          placeholder="请输入创建日期"
          input-align="right"
          readonly
        >
          <p slot="label">创建日期</p>
        </van-field>

        <van-field
          v-model="mayFinishDate"
          :disabled="editOrView"
          type="text"
          placeholder="请输入应完成日期"
          input-align="right"
          readonly
        >
          <p slot="label">应完成日期</p>
        </van-field>
      </van-cell-group>
      <p class="inp_top tip-text">{{$route.query.item.approveStatus==1?"请按要求进行抵押资料的上传":"抵押资料的上传"}}</p>
      <van-cell-group class="inp_top">
        <div class="bj">
          <van-cell-group class="top10">
            <van-field readonly type="text" @click-right-icon="addSupervisoryFile">
              <p slot="label">
                抵押资料
                <span class="required" v-if="editOrView">*</span>
              </p>
              <p slot="right-icon" v-if="editOrView">
                <van-icon name="plus" color="#40A9FF" />
              </p>
            </van-field>
            <div class="van-filed">
              <uploader
                ref="supervisoryFile"
                :billType="{...billType}"
                :isEdit="editOrView"
                @uploadData="fileIdListFn"
                @fileIdListFn="fileIdListFn"
                @getImgIds="getImgIds"
              ></uploader>
            </div>
          </van-cell-group>
        </div>
      </van-cell-group>
    </div>
    <!-- -->
    <van-button v-if="editOrView" class="footer-btn" size="large" v-on:click="submits">提交</van-button>
  </div>
</template>

<script>
import moment from "moment";
import uploader from "../components/uploader.vue";
export default {
  name: "pledge_info",
  data() {
    return {
      imgId: [],
      resCode: "",
      editOrView: false,
      createDate:"",
      mayFinishDate:"",
      plateInfoData: {
        createDate: "",
        mayFinishDate: ""
      },
      billType: {
        attachmentGroup: 15, //数据字典
        base64FileInfoList: [],
        billId: this.$route.query.item,
        billTypeCode: "VFL0503"
      }
    };
  },

  created() {
    if (
      this.$route.query.superviseStatus == 1 &&
      this.$route.query.approveStatus == 1
    ) {
      this.editOrView = true;
    }
    this.plateInfo(this.$route.query.item, this.$route.query.superviseStatus);
    this.$store.commit("setSupManTab", this.$route.query.superviseStatus);
  },
  components: {
    uploader: uploader
  },
  methods: {
    // 组件加载时,如果有附件信息,将对应的Id保存;
    getImgIds(val){
      if(!!val){
        this.imgId=val[0];
      }
    },
    //组件点击上传时的回调,将对应的Id保存;
    fileIdListFn(data) {
      console.info("上传回调:",data);
      this.imgId = data;
    },
    // 点击加号上传;
    addSupervisoryFile() {
      this.$refs.supervisoryFile.isShow = true;
    },
    // 查询 抵质押页面信息;
    plateInfo: function(id, superviseStatus) {
      let oThis=this;
      this.callServer({
        params: {
          reqmethod: "postjson",
          requrl: `/imfbp-vfl-app-web/vflSupervise/getVflSuperviseById`,
          reqparam: { id, superviseStatus }
        }
      })
        .then(res => {
          if (res.detailMsg.data) {
            let resData=res.detailMsg.data;
            // new Date(res.detailMsg.data.createDate) || new Date();
            oThis.createDate =  resData.createTime && resData.createTime.split(" ")[0];
            oThis.mayFinishDate = resData.mayFinishDate;
            this.plateInfoData = resData;
          }
        })
        .catch(err => {
          console.log(err);
        });
    },

    // 返回
    back() {
      this.$store.commit("setSupManTab", this.$route.query.superviseStatus);
      this.$router.push({
        name: "supervisory_interface",
        query: { superviseStatus: this.$route.query.superviseStatus }
      });
    },

    // 提交表单操作;
    submits() {
      let self = this;
      this.plateInfoData.fileIdList = [];
      // this.plateInfoData.fileIdList = this.$store.state.imgLen;
      this.plateInfoData.fileIdList=this.imgId;
      if (!(this.plateInfoData.fileIdList && this.plateInfoData.fileIdList.length)) {
        this.$toast("请上传抵押资料");
        return;
      }

      self.$toast.loading({
        duration: 0,
        forbidClick: true, // 禁用背景点击
        message: "提交中..."
      });

      // http://dev-test.iuap.imfbp.com/eiap-plus/appResAllocate/queryBpmTemplateAllocate?funccode=VFL0302&nodekey=VFL0302
      self
        .callServer({
          params: {
            reqmethod: "get",
            requrl:
              "/eiap-plus/appResAllocate/queryBpmTemplateAllocate?funccode=VFL0503&nodekey=VFL0503&_=" +
              moment().valueOf(),
            reqparam: ""
          }
        })
        .then(res => {
          if (res.detailMsg.data.res_code) {
            self.resCode = res.detailMsg.data.res_code;
            let idList = [{ id: self.$route.query.item }];
            // self.plateInfoData.fileIdList = ['7E60075NME4F1B0001UU'];
            // console.log(self.imgId);
            // self.plateInfoData.fileIdList=self.imgId
            // self.plateInfoData.superviseStatus = "2";
            // self.plateInfoData.idList=idList
            self
              .callServer({
                params: {
                  reqmethod: "postjson",
                  requrl:
                    "/imfbp-vfl-app-web/vflSuperviseBpm/saveAndSubmit?processDefineCode=" +
                    res.detailMsg.data.res_code,
                  reqparam: self.plateInfoData
                }
              })
              .then(res => {
                if (res.success) {
                  self.$toast(res.message);
                  self.$router.push({
                    name: "supervisory_interface",
                    query: {
                      superviseStatus: self.$route.query.superviseStatus
                    }
                  });
                }
              })
              .catch(err => {
                self.$toast("请求失败");
                self.$toast.clear();
              });
          } else {
            self.$toast("单据没有关联流程!");
          }
        })
        .catch(err => {
          self.$toast("无法获取code值");
          setTimeout(self.$toast.clear(), 1000);
          console.log(err);
        });
    }
  }
};
</script>

<style scoped>
.tip-text {
  font-size: 13px;
  font-family: PingFangSC;
  margin: 8px 0 8px 16px;
  color: #2d2d2d;
}
.ple-sub {
  width: 375px;
  height: 331px;
}

.footer-btn {
  position: fixed;
  bottom: 0;
  left: 0;
  border: 1px solid #40a9ff;
}
</style>