
<template>
  <div class="imgList">
    <template v-if="fileList">
      <div class="imgBox" v-for="(item,index) in fileList" :key="item.ID">
        <van-image
          class="uploaderImg"
          fit="cover"
          radius="4"
          :src="item.src"
          v-on:click="previewImg(item,index)"
        />
        <i
          class="iconfont iconzj-refuse"
          v-if="isEdit"
          width="24"
          height="24"
          v-on:click="deleteImg(item,index)"
        ></i>
        <van-image-preview
          v-model="isPreview"
          :images="images"
          @close="closeImgPreview"
          :start-position="startPosition"
        ></van-image-preview>
      </div>
    </template>
    <van-popup v-model="isShow" position="bottom">
      <div class="ocr-box">
        <div class="ocr-row" @click="getLibrary()">
          <p class="ocr-row-1">
            <i class="iconfont iconzj-Album"></i>
          </p>
          <p class="ocr-row-2">照片</p>
        </div>
        <div class="ocr-row" @click="getCamera()">
          <p class="ocr-row-1">
            <i class="iconfont iconzj-shot"></i>
          </p>
          <p class="ocr-row-2">拍照</p>
        </div>
      </div>
      <van-button type="default" size="large" @click="cancelUpload">取消</van-button>
    </van-popup>
  </div>
</template>
<script>
import global_config from "../api/globalConfig";
export default {
  props: ["billType", "isEdit"],
  components: {},
  data: function() {
    return {
      isShow: false,
      isPreview: false,
      fileList: [],
      images: [],
      startPosition: 0,
      imgIds: []
    };
  },
  // watch:{
  //   'imgIds.length'(){
  //       this.$emit('fileIdListFn',this.imgIds)
  //   }
  // },
  created() {
    this.getImgList();
  },
  mounted() {
    // let a=JSON.parse(JSON.stringify(this.imgIds.__ob__))
    // console.log(this.imgIds,"微软发文发问 违法我");
    //         this.$store.commit("getImgLen",[],this.imgIds)
    //     this.$emit('fileIdListFn',this.imgIds)
  },
  methods: {
    //拍照
    getCamera() {
      let oThis = this;
      let data = {
        params: {
          transtype: "use_camera"
        }
      };
      this.callNative("gotoNative", data, function(res) {
        let img = res.photostring[0].bitmapToBase64;
        let attachmentName = res.photostring[0].imagePath;
        let attachmentType = res.photostring[0].imageType;
        oThis.billType.base64FileInfoList = [
          {
            attachmentName: attachmentName,
            attachmentType: attachmentType,
            base64Content: img
          }
        ];
        oThis.isShow = false;
        oThis.uploaderImg();
      });
    },
    //从相册选择
    getLibrary() {
      let oThis = this;
      let data = {
        params: {
          transtype: "use_album"
        }
      };
      this.callNative("gotoNative", data, function(res) {
        oThis.billType.base64FileInfoList = [];
        res.photostring.forEach(function(item, index) {
          let list = {
            attachmentName: item.imagePath,
            attachmentType: item.imageType,
            base64Content: item.bitmapToBase64
          };
          oThis.billType.base64FileInfoList.push(list);
        });
        oThis.isShow = false;
        oThis.uploaderImg();
      });
    },
    // 获取图片后上传/保存图片;
    uploaderImg: function() {
      let oThis = this;
      let reqparam = {
        attachmentGroup: this.billType.attachmentGroup,
        base64FileInfoList: this.billType.base64FileInfoList,
        billId: this.billType.billId,
        billTypeCode: this.billType.billTypeCode
      };
      this.callServer({
        params: {
          reqmethod: "postjson",
          requrl: "/imfbp-bs-file-web/bossAttachment/uploadMultiFileByBase64",
          reqparam: reqparam
        }
      })
        .then(res => {
          if (res.success) {
            res.detailMsg.data.forEach(function(item, index) {
              let fileImg = {
                id: item.id,
                src: global_config.nc_url + "/" + item.attachmentUrl
              };
              let imgSrc = global_config.nc_url + "/" + item.attachmentUrl;
              oThis.fileList.push(fileImg);
              oThis.images.push(imgSrc);
              oThis.$store.commit("getImgLen", [item.id]);
            });
            oThis.$emit("uploadData", res.detailMsg.idListData);
          }
        })
        .catch(err => {
          oThis.$toast("上传失败");
        });
    },
    cancelUpload: function() {
      this.isShow = false;
    },
    previewImg: function(item, index) {
      this.startPosition = index;
      this.isPreview = true;
    },
    closeImgPreview: function() {},
    deleteImg: function(item, index) {
      debugger;
      let oThis = this;
      let idList = [];
      idList.push(item.id);
      this.$dialog
        .confirm({
          title: "提示",
          message: "确定删除这张图片吗？"
        })
        .then(() => {
          oThis
            .callServer({
              params: {
                reqmethod: "postjson",
                requrl:
                  "/imfbp-bs-file-web/bossAttachment/logicDeleteBossAttachmentByBatchId",
                reqparam: { idList: idList }
              }
            })
            .then(res => {
              if (res.success == "true") {
                oThis.fileList.splice(index, 1);
                oThis.images.splice(index, 1);
                oThis.imgIds.splice(index, 1);
                oThis.$emit("fileIdListFn", oThis.imgIds);
              }
            })
            .catch(err => {
              // oThis.$toast("请求失败");
            });
        })
        .catch(() => {
          // on cancel
        });
    },
    // 组件加载,获取已经上传的附件;
    getImgList() {
      let oThis = this;
      let billId = this.billType.billId;
      this.callServer({
        params: {
          reqmethod: "postjson",
          requrl: "/imfbp-bs-file-web/bossAttachment/getAllByGroup",
          reqparam: { billId: billId }
        }
      })
        .then(res => {
          if (res.success) {
            res.detailMsg.data.forEach(function(item, index) {
              if (oThis.billType.attachmentGroup == item.attachmentGroup) {
                item.bossAttachmentRpcList.forEach(function(item2, index2) {
                  let fileImg = {
                    id: item2.id,
                    src: global_config.nc_url + "/" + item2.attachmentUrl
                  };
                  let imgSrc = global_config.nc_url + "/" + item2.attachmentUrl;
                  oThis.fileList.push(fileImg);
                  oThis.images.push(imgSrc);
                  oThis.imgIds.push(item2.id);
                });
                oThis.imgIds = res.detailMsg.data.map(r => {
                  return [...r.bossAttachmentRpcList.map(br => br.id)];
                });
                oThis.$emit("getImgIds",oThis.imgIds)
                oThis.$store.commit("getImgLen", [], oThis.imgIds[0]);

              }
            });
            // oThis.fileList.push();
          }
        })
        .catch(err => {
          // oThis.$toast("请求失败");
        });
    }
  }
};
</script>
<style scoped>
.imgList {
  margin-top: 10px;
  padding: 0 0 0 20px;
}
.uploaderImg {
  margin-right: 20px;
  margin-bottom: 20px;
  width: calc((100vw - 120px) / 5) !important;
  height: calc((100vw - 120px) / 5) !important;
}
.imgBox {
  position: relative;
  display: inline-block;
}
.iconzj-refuse {
  position: absolute;
  top: -8px;
  right: 12px;
  color: #ff595f;
}
.ocr-box {
  display: flex;
  padding: 30px 0;
}
.ocr-row {
  flex: 1;
}
.ocr-row-1 {
  width: 64px;
  height: 64px;
  background: #f0f0f5;
  text-align: center;
  line-height: 64px;
  border-radius: 50%;
  margin: 0 auto;
}
.ocr-row-2 {
  font-size: 14px;
  color: #2d2d2d;
  height: 30px;
  line-height: 30px;
  width: 100%;
  text-align: center;
}
.iconzj-Album {
  font-size: 30px;
  color: rgb(255, 169, 65);
}
.iconzj-shot {
  font-size: 30px;
  color: rgb(50, 150, 250);
}
</style>